@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
.App {
  text-align: center;
  height: 100vh;
  padding: 0 20px 20px;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #3b3b3b;
  background-color: #ffffff;
  background-image: url("data:image/svg+xml,%3Csvg width='6' height='6' viewBox='0 0 6 6' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%2351bac9' fill-opacity='0.34' fill-rule='evenodd'%3E%3Cpath d='M5 0h1L0 6V5zM6 5v1H5z'/%3E%3C/g%3E%3C/svg%3E");
}

.title {
  font-size: 36px;
  font-weight: 700;
  padding: 30px;
  color: #505050;
  text-transform: uppercase;
}

.card_item {
    -webkit-transition: -webkit-transform 0.3s;
    transition: -webkit-transform 0.3s;
    transition: transform 0.3s;
    transition: transform 0.3s, -webkit-transform 0.3s;
    padding: 20px 10px;
}

.card_item:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.card-text {
    padding-top: 20px;
    font-size: 20px;
    text-transform: uppercase;
}

.green_bg {
    background-color: rgba(119, 221, 119, 0.5);
}

.red_bg {
    background-color: rgba(240, 122, 93, 0.5);
}

.todo-list {
    list-style: none;
    margin: 50px 0;
    padding: 0;
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr) ) ;
}

.form {
  max-width: 450px;
  margin: 0 auto;
}

.control {
  display: flex;
  justify-content: center;
}
